
html {
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
}
html *,
html *:before,
html *:after {
  -webkit-box-sizing: inherit;
  box-sizing: inherit;
}
html *:before,
html *:after {
  z-index: -1;
}
body,
p,
div,
h1,
h2,
h3,
h4,
h5,
h6,
ul,
ol,
li,
table,
th,
td,
form,
fieldset,
legend,
input,
textarea,
button,
select,
a,
span,
label,
section,
article,
canvas,
audio,
video,
footer,
nav,
input,
img,
dd,
dl,
dt {
  margin: 0;
  padding: 0;
  max-height: 99999999px;
}
body {
  position: relative;
  width: 100%;
  margin: 0 auto;
  color: #333;
  font-family: "Microsoft YaHei", "Helvetica Neue", Helvetica, "Hiragino Sans GB", Arial, sans-serif, iconfont;
  background-color: #FFF;
  -webkit-user-select: none;
  -moz-user-select: none;
   -ms-user-select: none;
       user-select: none;
}
input,
textarea,
a {
  outline: none;
  -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
  -webkit-appearance: none;
}
a:link,
a:visited,
a:active {
  text-decoration: none;
  color: #333;
}
ul {
  list-style: none;
}
select {
  outline: none;
}
select:focus {
  outline: none;
}
i {
  font-style: normal;
}
/*旧版CSS3方法*/
