body {
  min-width: 1170px;
}
.dn {
  display: none !important;
}
[v-cloak] {
  display: none;
}
.header {
  z-index: 9;
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 110px;
  background-color: rgba(70, 156, 197, 0.8);
}
.header .main-header {
  width: 1170px;
  height: 100%;
  margin: 0 auto;
  display: -webkit-box;
  display: box;
  -webkit-box-align: center;
  box-align: center;
  -webkit-box-pack: justify;
  box-pack: justify;
}
.header .logo {
  width: 155px;
  height: 80px;
  margin-left: 10px;
  background: url(../asset/img/logo.png) no-repeat;
  background-size: 100%;
}
.header .nav {
  width: 700px;
  height: 100%;
  display: -webkit-box;
  display: box;
  -webkit-box-pack: center;
  box-pack: center;
  -webkit-box-align: center;
  box-align: center;
}
.header .nav .nav-item {
  width: 100px;
  height: 100%;
  color: #FFF;
  text-align: center;
  border-top: 3px solid transparent;
  display: -webkit-box;
  display: box;
  -webkit-box-pack: center;
  box-pack: center;
  -webkit-box-align: center;
  box-align: center;
}
.header .nav .nav-item:hover,
.header .nav .nav-item.active {
  border-top: 3px solid #bbd005;
}
.header .other-frame {
  width: 180px;
  height: 100%;
  margin-right: 20px;
  display: -webkit-box;
  display: box;
  -webkit-box-pack: center;
  box-pack: center;
  -webkit-box-align: center;
  box-align: center;
  -webkit-box-orient: vertical;
  box-orient: vertical;
}
.header .other-frame .share-frame {
  width: 100%;
  height: 50px;
  display: -webkit-box;
  display: box;
  -webkit-box-pack: center;
  box-pack: center;
  -webkit-box-align: center;
  box-align: center;
}
.header .other-frame .share-frame a {
  display: block;
  margin-right: 5px;
  color: #FFF;
  cursor: pointer;
  font-size: 24px;
}
.header .other-frame .share-frame a span {
  font-size: 24px;
}
.header .other-frame .language {
  width: 90px;
  height: 30px;
  padding-left: 10px;
  color: #FFF;
  cursor: pointer;
  border: none;
  border-radius: 3px;
  background-color: #50a2cb;
}
.header .other-frame .language option {
  width: 100%;
  color: #333;
  background-color: #FFF;
}



