body {
  min-width: 1170px;
}
.dn {
  display: none !important;
}
[v-cloak] {
  display: none;
}
.footer {
  width: 100%;
  height: 260px;
  background: url(../asset/img/footer_bg.jpg) center 0 no-repeat #1e8fce;
}
.footer .main-footer {
  width: 1170px;
  height: 210px;
  margin: 0 auto;
  color: #FFF;
  font-size: 14px;
  display: -webkit-box;
  display: box;
  -webkit-box-pack: center;
  box-pack: center;
  -webkit-box-align: center;
  box-align: center;
}
.footer .main-footer .footer-item {
  width: 300px;
  height: 160px;
}
.footer .main-footer .footer-item .big-font {
  font-size: 20px;
}
.footer .main-footer .footer-item p {
  font-size: 13px;
  letter-spacing: 1px;
  line-height: 28px;
}
.footer .main-footer .footer-logo {
  width: 220px;
  background: url(../asset/img/logo.png) 0 center no-repeat;
}
.footer .main-footer .footer-intro {
  width: 360px;
  display: -webkit-box;
  display: box;
  -webkit-box-pack: center;
  box-pack: center;
  -webkit-box-orient: vertical;
  box-orient: vertical;
}
.footer .main-footer .subscription {
  width: 260px;
  display: -webkit-box;
  display: box;
  -webkit-box-pack: center;
  box-pack: center;
  -webkit-box-orient: vertical;
  box-orient: vertical;
}
.footer .main-footer .subscription .email-frame {
  position: relative;
  width: 200px;
  height: 30px;
  margin-top: 8px;
  border: 1px solid #FFF;
  border-radius: 3px;
}
.footer .main-footer .subscription .email-frame input {
  display: block;
  position: absolute;
  top: 0;
  left: 0;
  width: 170px;
  height: 30px;
  color: #FFF;
  text-indent: 8px;
  border: none;
  background: none;
}
.footer .main-footer .subscription .email-frame span {
  display: block;
  position: absolute;
  top: 2px;
  right: 2px;
  width: 24px;
  height: 24px;
  cursor: pointer;
  font-size: 20px;
}
.footer .main-footer .footer-other {
  width: 240px;
  display: -webkit-box;
  display: box;
  -webkit-box-pack: center;
  box-pack: center;
  -webkit-box-align: end;
  box-align: end;
  -webkit-box-orient: vertical;
  box-orient: vertical;
}
.footer .main-footer .footer-other .share-frame {
  width: 100%;
  height: 40px;
  display: -webkit-box;
  display: box;
  -webkit-box-pack: end;
  box-pack: end;
}
.footer .main-footer .footer-other .share-frame a {
  display: block;
  margin-right: 5px;
  color: #FFF;
  font-size: 24px;
  cursor: pointer;
}
.footer .main-footer .footer-other .share-frame a span {
  font-size: 24px;
}
.footer .copyright {
  width: 100%;
  height: 50px;
  line-height: 50px;
  font-size: 12px;
  color: #FFF;
  text-align: center;
}



